#RequestPage {
    .filters-container {
        gap: 15px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
    .flatpickr-clear-btn {
        position: relative ;

        .btn-icon-flatpickr {
            top:5px;
            right: 1px;
            position: absolute;
        }

        .intp-flatpick {
            opacity         : 1 !important;
            background-color : white !important;
    
            // &::placeholder {
            //     color: #6e6b7b;
            // }
        }
    }

}