@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";

#RequestTable {

    .title {
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #5E5873;
    }

    .dropdown-style {
        z-index: 99;
    }
    .last-column {
        text-align: end;
    }

    .group {
        @include displayRow(5px, start, center);

    }

    .request-type {
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
        color: #FF784F;

    }
    .request-type-color-1 {
        color : #6D711E !important;
    }
    .request-type-color-2 {
        color : #1B9AAA !important;
    }

    .actions-wrapper {
        @include displayRow(5px, end, center);

    }

    .key-text {
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        text-transform: uppercase;
        color: #2C2C26;
    }

    .eidt-icon {
        color: #575975;
    }

    .btn-dropdown {
        padding: 0;
        width: fit-content;
        background-color: transparent;

        .badge-style {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #6D711E !important;
            background: rgba(109, 113, 30, 0.12) !important;

            .text {
                font-weight: 600;
                font-size: 13px;
                line-height: 18px;
            }
        }

        .badge-style-1 {
            color: #3554D1 !important;
            background: rgba(53, 84, 209, 0.12) !important;
        }
        
        .badge-style-2 {
            border: 1px solid #999999 !important;
            background-color: #cccccc !important;
            color: #666666 !important;
        }
    }
}